import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import uniPop from '@/components/uniPop.vue';
import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniLoadMore,
		uniPop,
		uniPopup
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition: "fixed",
			headerTop: "0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0,
			// cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			goodsList: [],
			url: this.$ctx,
			pageNumber: 1,
			likeTitle: '',
			twoIndustryId: null,
			mobile: uni.getStorageSync(`mobile`),
			flist: [],
			slist: [],
			companyId: uni.getStorageSync(`companyId`),
			cheapPurchaseCompanyId: uni.getStorageSync(`cheapPurchaseCompanyId`),
			classification: uni.getStorageSync(`classification`),
		};
	},

	onLoad(options) {



		this.likeTitle = options.likeTitle;
		this.twoIndustryId = options.twoIndustryId;
		this.loadData();
		this.loadIndustry()
	},
	onShow() {
		this.login(); //微信登录
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom() {
		this.loadData();
	},
	methods: {
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type = 'add', loading) {
			//没有更多直接返回
			if (type === 'add') {
				if (this.loadingType === 'nomore') {
					return;
				}
				this.loadingType = 'loading';
			} else {
				this.loadingType = 'more'
				this.pageNumber = 1
			}

			let data = {
				data: JSON.stringify({
					pageNumber: this.pageNumber,
					likeTitle: this.likeTitle,
					twoIndustryId: this.twoIndustryId
				})
			}
			this.$getJson('/api/v2/vue/ydd/index/resList.jsp', data, 'POST', res => {
				console.log('----list------------', res);
				if (res.data) {
					if (this.pageNumber == 1) {
						this.goodsList = res.data;
					} else {
						this.goodsList = this.goodsList.concat(res.data);
					}
					this.pageNumber += 1
					//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
					this.loadingType = res.data.length < 8 ? 'nomore' : 'more';
					if (type === 'refresh') {
						if (loading == 1) {
							uni.hideLoading()
						} else {
							uni.stopPullDownRefresh();
						}
					}
				}
			});
			//筛选，测试数据直接前端筛选了
			/* if(this.filterIndex === 1){
				goodsList.sort((a,b)=>b.sales - a.sales)
			}
			if(this.filterIndex === 2){
				goodsList.sort((a,b)=>{
					if(this.priceOrder == 1){
						return a.price - b.price;
					}
					return b.price - a.price;
				})
			} */

		},
		async loadIndustry() {
			this.$getJson('/api/v2/vue/ydd/common/getIndustry.jsp', {}, 'POST', res => {
				console.log('----getIndustry------------', res);
				res.data.list.forEach(item => {
					// if(!item.pid){
					// 	this.flist.push(item);  //pid为父级id, 没有pid或者pid=0是一级分类
					// }else if(!item.picture){
					// 	this.slist.push(item); //没有图的是2级分类
					// }else{
					// 	this.tlist.push(item); //3级分类
					// }
					// 
					if (item.level == 1) {
						item.show = "+"
						this.flist.push(item); //pid为父级id, 没有pid或者pid=0是一级分类
					} else {
						item.checked = false
						this.slist.push(item); //有图的是2级分类
					}
				})
			});
		},
		showChild(ite) {
			ite.show = ite.show == "+" ? "-" : "+"
			this.slist.forEach(item => {
				if (item.parentId == ite.id) {
					if (item.checked == false) {
						item.checked = true
					} else {
						item.checked = false
					}
				}
			})
		},
		//筛选点击
		tabClick(index) {
			if (this.filterIndex === index && index !== 2) {
				return;
			}
			this.filterIndex = index;
			if (index === 2) {
				this.priceOrder = this.priceOrder === 1 ? 2 : 1;
			} else {
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//显示分类面板
		toggleCateMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(() => {
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item) {
			this.twoIndustryId = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.resource.id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}` + "&companyId=" + this.companyId +
					"&cheapPurchaseCompanyId=" + this.cheapPurchaseCompanyId + "&classification=" + this.classification
			})
		},
		stopPrevent() {},
		//加入购物车
		addCart(item) {
			if (!uni.getStorageSync(`mobile`)) {
				this.login()
				//弹出登录
				this.$refs.popup.open()
				//防止点击遮罩关闭
				this.$refs.popup.clickClose(true)
				return
			}
			//加入购物车
			let data = {
				data: JSON.stringify({
					goodsId: item.goods.id,
					memberId: uni.getStorageSync("userInfo").id,
					cheapPurchaseCompanyId: uni.getStorageSync('cheapPurchaseCompanyId')
				})
			}
			this.$getJson('/api/v2/vue/ydd/cart/addCart.jsp', data, 'POST', res => {
				console.log('----res------------', res);
				uni.switchTab({
					url: `/pages/cart/cart`
				})
			});
		},
		//登录
		login() {
			var _this = this
			if (!this.mobile) {
				wx.login({
					success: res => {
						console.info('res', res)
						_this.loginCode = res.code
						_this.$refs.uniPop.show({
							title: '登录',
							content: '是否同意获取手机号？',
							shade: true,
							shadeClose: true,
							time: 5,
							anim: 'fadeIn',
							isVisible: true,
							position: 'bottom',
							loginCode: res.code,
							isUrl: false,
						})
					}
				})
			}
		},
		//登录回显
		childClick(e) {
			/* console.log(e)
			uni.setStorageSync('userInfo', e);
			this.userInfo = e;
			this.mobile = e.mobile; */
			// this.$refs.popup.close()
			if (e == true) {
				this.$refs.popup.close()
			} else {
				this.mobile = e.mobile
				this.$refs.popup.close()
			}
		},
	},
}
